<template>
  <div class="instrument-panel">
    <div class="ontop">
      <span class="text"><slot name="title"></slot></span>
      <span class="icon"><slot name="icon"></slot></span>
    </div>
    <div class="chart" ref="chart" @resize="handleResize"></div>
    <div class="panel-footer"></div>
  </div>
</template>

<script>
export default {
  props: {
    myOption: Object,
    name: String,
    unit: String,
    max: Number,
    data: Number,
    leftScal: Number,
    rightScal: Number,
  },
  data() {
    return {
      myChart: null,
    };
  },
  methods: {
    init(newVal) {
      this.$emit("initData", newVal);
      this.myChart.setOption(this.myOption);
      this.myChart.setOption({
        series: [
          {
            color:
              this.data < this.leftScal
                ? "#49afff"
                : this.data < this.rightScal
                ? "#68A54A"
                : "#f56c6c",
          },
          {},
          {
            detail: {
              color:
                this.data < this.leftScal
                  ? "#49afff"
                  : this.data < this.rightScal
                  ? "#68A54A"
                  : "#f56c6c",
              fontSize: this.unit === "ppm" ? 22 : 30,
              rich: {
                a: {
                  color:
                    this.data < this.leftScal
                      ? "#49afff"
                      : this.data < this.rightScal
                      ? "#68A54A"
                      : "#f56c6c",
                },
              },
            },
            data: [
              {
                value: newVal,
              },
            ],
            axisLine: {
              lineStyle: {
                color: [
                  //数组第一个属性是颜色所占line百分比
                  [(this.leftScal / this.max).toFixed(2), "#49afff"], //TODO数据更改
                  [(this.rightScal / this.max).toFixed(2), "#68A54A"], //TODO数据更改
                  [1, "#f56c6c"],
                ],
              },
            },
          },
        ],
      });
    },
    handleResize() {
      // 窗口大小改变时，调整图表大小
      if (this.myChart) {
        this.myChart.resize();
      }
    },
    //更新图表
    refreshChart(chart, name, max, data, leftScal, rightScal, unit) {
      let option = {
        tooltip: {
          formatter: function () {
            return "当前" + name + ":" + data + unit;
          },
        },
        angleAxis: {
          max: max / 0.75,
        },
        series: [
          {
            data: [0, 0, data],
            color:
              data < leftScal
                ? "#49afff"
                : data < rightScal
                ? "#68A54A"
                : "#f56c6c",
          },
          {
            data: [0, 0, max],
          },
          {
            detail: {
              formatter: function () {
                return [data + unit, "{a|当前" + name + "}"].join("\n");
              },
              color:
                data < leftScal
                  ? "#49afff"
                  : data < rightScal
                  ? "#68A54A"
                  : "#f56c6c",
              fontSize: unit === "ppm" ? 22 : 30,
              rich: {
                a: {
                  color:
                    data < leftScal
                      ? "#49afff"
                      : data < rightScal
                      ? "#68A54A"
                      : "#f56c6c",
                },
              },
            },
            max: max,
            data: [
              {
                value: data,
              },
            ],
            axisLine: {
              lineStyle: {
                color: [
                  //数组第一个属性是颜色所占line百分比
                  [(leftScal / max).toFixed(2), "#49afff"], //TODO数据更改
                  [(rightScal / max).toFixed(2), "#68A54A"], //TODO数据更改
                  [1, "#f56c6c"],
                ],
              },
            },
            axisLabel: {
              show: true,
              formatter: function (value) {
                if (value === 0 || value === max) {
                  return value + unit;
                }
                return "";
              },
            },
          },
        ],
      };
      chart.setOption(option);
    },
  },
  created() {},
  mounted() {
    this.myChart = this.$echarts.init(this.$refs.chart);
    window.addEventListener("resize", this.handleResize);
  },
  watch: {
    data(newVal) {
      if (!this.myChart.getOption()) {
        this.init(newVal);
      } else {
        this.refreshChart(
          this.myChart,
          this.name,
          this.max,
          this.data,
          this.leftScal,
          this.rightScal,
          this.unit
        );
      }
    },
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.chart {
  height: 25vh;
  transform: scale(1);
}
.instrument-panel {
  position: relative;
  width: 100%;
  height: 30vh;
}

.ontop {
  position: relative;
  background: linear-gradient(to bottom, #0c252f80, #025feb32);
}

.ontop .text {
  height: 0.6rem;
  color: #fff;
  line-height: 0.6rem;
  text-align: center;
  font-size: 0.25rem;
  font-weight: 400;
  position: relative;
  left: 70px;
}

.ontop .icon img {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 54px;
  top: 5px;
}

.panel-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.ontop::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  content: "";
}

.ontop::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  content: "";
}

.instrument-panel .panel-footer::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 10px;
  width: 10px;
  border-left: 2px solid #02a6b5;
  border-bottom: 2px solid #02a6b5;
}

.instrument-panel .panel-footer::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 10px;
  width: 10px;
  border-right: 2px solid #02a6b5;
  border-bottom: 2px solid #02a6b5;
}
</style>
