<template>
  <div class="showbox-main1 category-box" id="category1">
    <div class="predict">
      <div class="predict-box">
        <!-- <h2>温室环境预测</h2> -->
        <div class="predict-show">
          <div class="chart">
            <div id="line1"></div>
          </div>
          <div class="chart">
            <div id="line2"></div>
          </div>
          <div class="chart">
            <div id="line3"></div>
          </div>
          <div class="chart">
            <div id="line4"></div>
          </div>
          <div class="chart">
            <div id="line5"></div>
          </div>
          <div class="chart">
            <div id="line6"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="midbox">
      <div class="rank" ref="rank"></div>
      <div class="explain">
        <div>
          <div class="explain-content">预测植物长势良好</div>
          <div class="robot"></div>
        </div>
      </div>
    </div>
    <!-- ********************** -->
  </div>
</template>

<script>
import option from "@/assets/js/echartOptions/liquidOption.js";
export default {
  components: {
  },
  data() {
    return {
      option: option,
    };
  },
  methods: {},
  created() {
    let option = {
      series: [
        {
          color: [
            new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "#2e9dc5",
              },
              {
                offset: 1,
                color: "#126f8d",
              },
            ]),
          ],
        },
      ],
    };
    this.option = this.$echarts.util.merge(this.option, option);
  },
  mounted() {
    this.myChart = this.$echarts.init(this.$refs.rank);
    this.$nextTick(() => {
      this.myChart.setOption(this.option);
      let option = {
        series: [
          {
            color: [
              new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "#2e9dc5",
                },
                {
                  offset: 1,
                  color: "#126f8d",
                },
              ]),
            ],
          },
        ],
      };
      this.myChart.setOption(option);
    });
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.selected {
  display: none;
}

.showbox-main1 {
  display: flex;
  width: 100%;
  height: 100%;
}

.predict {
  width: 900px;
  height: 750px;
  background-color: rgba(53, 163, 207, 0.397);
  border-radius: 5%;
  margin-top: 20px;
}

/* 大盒子里有六个小盒子，分为两行三列，使用grid布局 */
.predict-show {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 20px; /* 设置行和列的间隔 */
  padding: 10px; /* 如果有必要，可以设置外边距 */
  padding-top: 15px;
}

.predict-show div {
  width: 430px;
  height: 230px;
}

.midbox {
  width: 400px;
  height: 750px;
  /* background-color: pink; */
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 20px;
}

.midbox .rank {
  height: 450px;
  /* background-color: white; */
}

.midbox .explain {
  width: 100%;
  height: 300px;
}
.midbox .explain div {
  position: relative;
}
.midbox .explain div:first-child {
  margin-left: 80px;
  height: 200px;
  width: 200px;
  background-image: url("@/assets/images/warnning/explain.png");
  background-size: contain;
}

.midbox .explain div .explain-content {
  padding: 10px;
  float: auto;
  width: 150px;
  height: 150px;
  margin-left: 26px;
}
</style>