import VueRouter from "vue-router";
import Vue from "vue";
import PlantIndex from "@/views/PlantIndex.vue";
import PlantWatch from "@/views/PlantWatch.vue";
import PlantAuto from "@/views/PlantAuto.vue";
import PlantWarn from "@/views/PlantWarn.vue";
import PlantManage from "@/views/PlantManage.vue";
import PlantManage2 from "@/views/PlantManage2.vue";
import PlantWarnPredict from "@/views/PlantWarnPredict.vue";
import PlantRbt from "@/views/PlantRbt.vue";
import RmtMonit from "@/views/watch/RmtMonit.vue";
import RpTher from "@/views/watch/RpTher.vue";
import StaReg from "@/views/watch/StaReg.vue";
import ManageIndex from "@/views/manage/ManageIndex.vue";
Vue.use(VueRouter);

const router = new VueRouter({
  routes: [
    { path: "/", redirect: "/index" },
    { path: "/index", component: PlantIndex },
    {
      path: "/watch",
      component: PlantWatch,
      redirect: "/watch/moniter",
      children: [
        {
          path: "/watch/moniter",
          component: RmtMonit,
        },
        {
          path: "/watch/replay",
          component: RpTher,
        },
        {
          path: "/watch/recognize",
          component: StaReg,
        },
      ],
    },
    {
      path: "/auto",
      component: PlantAuto,
    },
    {
      path: "/warn",
      component: PlantWarn,
      redirect: "/warn/predict",
      children: [
        {
          path: "/warn/predict",
          component: PlantWarnPredict,
        },
        {
          path: "/warn/robot",
          component: PlantRbt,
        },
      ],
    },
    {
      path: "/manage",
      redirect: "/manage/device",
      component: ManageIndex,
      children: [
        {
          path: "/manage/device",
          component: PlantManage,
        },
        {
          path: "/manage/user",
          component: PlantManage2,
        },
      ],
    },
  ],
  linkActiveClass: "active1",
  linkExactActiveClass: "active2",
});

export default router;
