<script>
import option from "@/assets/js/echartOptions/panelOption.js";
export default {
  data() {
    return {
      myOption: option,
      staticData: [
        {
          id: 1,
          name: "airTemp",
          title: "空气温度",
          icon: require("@/assets/images/icos/temp3.png"),
          unit: "℃",
        },
        {
          id: 2,
          name: "soilTemp",
          title: "土壤温度",
          icon: require("@/assets/images/icos/sm2.png"),
          unit: "℃",
        },
        {
          id: 3,
          name: "ltTens",
          title: "光照强度",
          icon: require("@/assets/images/icos/lt3.png"),
          unit: "lx",
        },
        {
          id: 4,
          name: "soilMoi",
          title: "土壤湿度",
          icon: require("@/assets/images/icos/st2.png"),
          unit: "%",
        },
        {
          id: 5,
          name: "airMois",
          title: "空气湿度",
          icon: require("@/assets/images/icos/am2.png"),
          unit: "%",
        },
        {
          id: 6,
          name: "CO2",
          title: "CO2浓度",
          icon: require("@/assets/images/icos/co2.png"),
          unit: "ppm",
        },
      ],
      dynamicData: [
        {
          max: 50,
          data: 30,
          leftScal: 20,
          rightScal: 30,
        },
        {
          max: 50,
          data: 25,
          leftScal: 20,
          rightScal: 30,
        },
        {
          max: 800,
          data: 300,
          leftScal: 200,
          rightScal: 600,
        },
        {
          max: 100,
          data: 60,
          leftScal: 50,
          rightScal: 70,
        },
        {
          max: 100,
          data: 30,
          leftScal: 50,
          rightScal: 60,
        },
        {
          max: 4000,
          data: 1500,
          leftScal: 2000,
          rightScal: 3000,
        },
      ],
      chartsOptions: [
        {
          name: "airTemp",
          id: 1,
          title: "空气温度",
          max: 50,
          data: 30,
          leftScal: 20,
          rightScal: 30,
          unit: "℃",
          icon: require("@/assets/images/icos/temp3.png"),
        },
        {
          name: "soilTemp",
          id: 2,
          title: "土壤温度",
          max: 50,
          data: 25,
          leftScal: 20,
          rightScal: 30,
          unit: "℃",
          icon: require("@/assets/images/icos/sm2.png"),
        },
        {
          name: "ltTens",
          id: 3,
          title: "光照强度",
          max: 800,
          data: 300,
          leftScal: 200,
          rightScal: 600,
          unit: "lx",
          icon: require("@/assets/images/icos/lt3.png"),
        },
        {
          name: "soilMoi",
          id: 4,
          title: "土壤湿度",
          max: 100,
          data: 60,
          leftScal: 50,
          rightScal: 70,
          unit: "%",
          icon: require("@/assets/images/icos/st2.png"),
        },
        {
          name: "airMois",
          id: 5,
          title: "空气湿度",
          max: 100,
          data: 30,
          leftScal: 50,
          rightScal: 60,
          unit: "%",
          icon: require("@/assets/images/icos/am2.png"),
        },
        {
          name: "CO2",
          id: 6,
          title: "CO2浓度",
          max: 4000,
          data: 1500,
          leftScal: 2000,
          rightScal: 3000,
          unit: "ppm",
          icon: require("@/assets/images/icos/co2.png"),
        },
      ],
    };
  },
  methods: {
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    get_env_values() {
      this.$axios({
        method: "get",
        url: "http://8.138.4.159/trandata",
      })
        .then((res) => {
          res = res.data;
          console.log(res);
          this.chartsOptions.forEach((el, i) => {
            el.main.data = res[i];
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    initData(data) {
      this.myOption.series[2].data[0].value = data;
    },
  },
  created() {
    console.log(
      `window width: ${window.innerWidth}, window height: ${window.innerHeight}`
    );

    this.$nextTick(() => {
      let datas = [
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 800),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 4000),
      ];
      this.chartsOptions.forEach((item, i) => {
        item.data = datas[i];
      });
    });
    setInterval(() => {
      let datas = [
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 50),
        this.getRandomInt(0, 800),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 100),
        this.getRandomInt(0, 4000),
      ];
      this.chartsOptions.forEach((item, i) => {
        item.data = datas[i];
      });
    }, 2000);
  },
};
</script>

