              <img src="@/assets/images/center-1.svg" title="大棚监控" />
            </router-link>
            <!-- <p>摄像头</p> -->
          </div>
        </li>
        <li>
          <img src="@/assets/images/center-linenoactive.png" />
          <div class="handle-con">
            <router-link to="/auto">
              <img src="@/assets/images/center-2.svg" title="智能设备" />
            </router-link>

            <!-- <p>智能设备</p> -->
          </div>
        </li>
        <li>
          <img src="@/assets/images/center-linenoactive.png" />
          <div class="handle-con">
            <router-link to="/warn">
              <img src="@/assets/images/center-3.svg" title="安全预警" />
            </router-link>
            <!-- <p>安全预警</p> -->
          </div>
        </li>
        <li>
          <img src="@/assets/images/center-linenoactive.png" />
          <div class="handle-con">
            <router-link to="/manage">
              <img src="@/assets/images/center-5.svg" title="管理系统" />
            </router-link>
            <!-- <p>管理系统</p> -->
          </div>
        </li>
      </ul>
    </div>
    <div class="sub">智 慧 种 植 平 台</div>
    <div class="showTime">{{ navTime ? navTime : clockTime() }}</div>
  </header>
</template>

<script scoped>
export default {
  data() {
    return {
      clockTimer: null,
      navTime: "",
    };
  },
  methods: {
    //获取时钟时间
    clockTime() {
      let dt = new Date();
      let y = dt.getFullYear();
      let mt = dt.getMonth() + 1;
      let day = dt.getDate();
      let h = dt.getHours(); //获取时
      let m = dt.getMinutes(); //获取分
      let s = dt.getSeconds(); //获取秒
      let t =
        "当前时间：" +
        y +
        "年" +
        mt +
        "月" +
        day +
        "日" +
        h +
        "时" +
        m +
        "分" +
        s +
        "秒";
      return t;
    },
  },
  created() {
    //打开时钟
    setInterval(() => {
      this.navTime = this.clockTime();
    }, 1000);
  },
};
</script>

<style scoped>
* {
