import Vue from "vue";
import App from "./App.vue";
import "@/assets/js/flexible.js";
import PlantWeather from "./components/utils/PlantWeather.vue";
import PlantEnvPanel from "./components/utils/PlantEnvPanel.vue";
import PlantSliderbar from "./components/utils/PlantSliderbar.vue";
import router from "./assets/router";
import * as echarts from "echarts"; //引入echarts

import "echarts-liquidfill";
import axios from "axios";

Vue.prototype.$echarts = echarts; //引入组件
Vue.prototype.$axios = axios;
Vue.config.productionTip = false;
Vue.component("PlantWeather", PlantWeather);
Vue.component("PlantEnvPanel", PlantEnvPanel);
Vue.component("PlantSliderbar", PlantSliderbar);
new Vue({
  render: (h) => h(App),
  router,
}).$mount("#app");
