<template>
  <div class="map">
    <div class="map1"></div>
    <div class="map2"></div>
    <div class="map3"></div>
    <div id="container">
      <div class="two">
        <div class="card" ref="card">
          <div class="search">
            <input
              type="text"
              class="search-bar"
              placeholder="Search"
              v-model="cityName"
              @keyup.enter="searchWeather(cityName)" />
            <button>
              <svg
                stroke="currentColor"
                fill="currentColor"
                stroke-width="0"
                viewBox="0 0 16 16"
                height="1em"
                width="1.5em"
                xmlns="http://www.w3.org/2000/svg">
                <path
                  fill-rule="evenodd"
                  d="M10.442 10.442a1 1 0 011.415 0l3.85 3.85a1 1 0 01-1.414 1.415l-3.85-3.85a1 1 0 010-1.415z"
                  clip-rule="evenodd"></path>
                <path
                  fill-rule="evenodd"
                  d="M6.5 12a5.5 5.5 0 100-11 5.5 5.5 0 000 11zM13 6.5a6.5 6.5 0 11-13 0 6.5 6.5 0 0113 0z"
                  clip-rule="evenodd"></path>
              </svg>
            </button>
          </div>
          <div class="weather loading">
            <div style="color: rgb(203, 195, 195)" class="notice">
              注意：请输入城市名拼音，首字母大写
            </div>
            <h2 class="city" style="color: rgb(203, 195, 195)">
              Weather in Hengyang
            </h2>
            <h1 class="temp" style="color: rgb(203, 195, 195)">21°C</h1>
            <div class="flex">
              <img
                src="https://openweathermap.org/img/wn/04d.png"
                alt=""
                class="icon" />
              <div class="description" style="color: rgb(203, 195, 195)">
                多云
              </div>
            </div>
            <div>
              <div class="humidity" style="color: rgb(203, 195, 195)">
                湿度: 60%
              </div>
              <div class="wind" style="color: rgb(203, 195, 195)">
                风速: 6.2 km/h
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VanillaTilt from "@/assets/js/vanilla-tilt.babel.js";
export default {
  data() {
    return {
      cityName: "",
      weather: null,
    };
  },
  methods: {
    searchWeather(cityName) {
      console.log(cityName);
      //查询天气
      this.weather.search();
    },
  },
  created() {
    this.weather = {
      apiKey: "13e786b2255800d48474a6fdbcd071dc",
      fetchWeather: function (city) {
        fetch(
          "https://api.openweathermap.org/data/2.5/weather?q=" +
            city +
            "&units=metric&appid=" +
            this.apiKey
        )
          .then((response) => response.json())
          .then((data) => this.displayWeather(data));
      },
      displayWeather: function (data) {
        const { name } = data;
        const { icon, description } = data.weather[0];
        const { temp, humidity } = data.main;
        const { speed } = data.wind;
        document.querySelector(".city").innerText = "Weather in " + name;
        document.querySelector(".icon").src =
          "https://openweathermap.org/img/wn/" + icon + ".png";
        document.querySelector(".description").innerText = description;
        document.querySelector(".temp").innerText = temp + "°C";
        document.querySelector(".humidity").innerText =
          "湿度: " + humidity + "%";
        document.querySelector(".wind").innerText = "风速: " + speed + " km/h";
        document.querySelector(".weather").classList.remove("loading");
      },
      search: function () {
        this.fetchWeather(document.querySelector(".search-bar").value);
      },
    };
  },
  mounted() {
    VanillaTilt.init(this.$refs.card, {
      max: 15,
      glare: true,
      reverse: true,
      "max-glare": 0.5,
      speed: 400,
    });
    this.weather.fetchWeather("Hengyang");
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  font-family: alimama;
}
@keyframes rotate1 {
  form {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes rotate2 {
  from {
    transform: translate(-50%, -50%) rotate(360deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(0deg);
  }
}
.map {
  position: relative;
  height: 100%;
  padding: calc(5%) 0;
}

.map .map1 {
  width: 6.475rem;
  height: 6.475rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(@/assets/images/map.png);
  background-size: 100% 100%;
  opacity: 0.3;
}

.map .map2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8.0375rem;
  height: 8.0375rem;
  background: url(@/assets/images/lbx.png);
  animation: rotate1 15s linear infinite;
  background-size: 100% 100%;
  opacity: 0.6;
}

.map .map3 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 7.075rem;
  height: 7.075rem;
  background: url(@/assets/images/jt.png);
  animation: rotate2 15s linear infinite;
  opacity: 0.6;
  background-size: 100% 100%;
}

.map .chart {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 7rem;
}

#container {
  height: 100%;
  width: 100%;
}

.two {
  /* display: flex; */
  height: 75%;
  top: 10%;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.card {
  position: relative;
  text-align: none;
  box-shadow: 0.25rem 0.25rem 0.625rem rgba(0, 0, 0, 0.7);
  overflow: hidden;
  border-radius: 0.3125rem;
  background: rgba(79, 155, 226, 0.151);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  padding: 0 0.6rem;
  width: 88%;
  height: 46vh;
  transform-style: preserve-3d;
  perspective: 12.5rem;
  backdrop-filter: blur(5px);
}

.search {
  height: 13vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}

.icon {
  animation: rotates 2.5s linear infinite alternate;
}

@keyframes rotates {
  from {
    transform: translate(-0.4em);
  }
  to {
    transform: translate(0.4em);
  }
}

button {
  margin: 0.5em;
  border-radius: 50%;
  border: none;
  height: 5vh;
  width: 5vh;
  outline: none;
  background: black;
  color: white;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

input.search-bar {
  height: 3vh;
  border: none;
  outline: none;
  padding: 0.5em 1em;
  border-radius: 24px;
  background: black;
  color: black;
  font-family: inherit;
  font-size: 120%;
  width: calc(70%);
  background: rgba(0, 0, 0, 0.1);
  transform: translateZ(30px);
  border-top: 1px solid rgba(255, 255, 255, 0.8);
  border-left: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(3px);
}

::placeholder {
  color: black;
  opacity: 0.7;
}

button:hover {
  border: white 0.2em solid;
  transition: 0.2s ease-in-out;
}

.card:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  from {
    transform: rotate(360deg);
  }
}

.notice {
  height: 6vh;
  font-size: 0.3rem;
}

h2.city {
  text-align: center;
  font-size: 0.6rem;
  transform: translateZ(30px);
}

h1.temp {
  margin: 0;
  font-size: 0.6rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-left: 0.1em;
  transform: translateZ(30px);
}

.description {
  text-transform: capitalize;
  line-height: 50px;
  margin-left: 0.1rem;
}

.humidity,
.wind,
.description {
  font-size: 20px;
}

.flex {
  display: flex;
  justify-content: left;
}

.weather.loading {
  visibility: hidden;
  max-height: 0.4rem;
  position: relative;
  align-items: center;
}

.weather.loading:after {
  visibility: visible;
  content: "Loading...";
  color: white;
  position: absolute;
  top: 0;
  left: 0.25rem;
}
</style>
